import {useState} from 'react'
import fightGif from './FightGIF.gif'
import constructGif from './ConstructGIF.gif'
import smithGif from './SmithGIF.gif'
import constructVideo from './ConstructVideo.mp4'
import smithVdieo from './SmithVideo.mp4'
import fightVideo from './FightVideo.mp4'
import './Content.css';
import './Content-mobile.css';

const Content=() => {
	const [shownContent,setShownContent] = useState({fight:false,construction:false,smithing:true});
	
	const manageShownContent=(contentToShow)=>{
		for(let cur in shownContent)
			shownContent[cur]=false;
		shownContent[contentToShow]=true;
		setShownContent({...shownContent});
	}
	// console.log(navigator.appVersion)
	// console.log(navigator.language)
	
  return (
    <div className="Content" id="Features" >
		<div className="ContentLeft">
			{	((navigator.appVersion.indexOf("Linux")!=-1)&&(navigator.appVersion.indexOf("OPR")!=-1))&&
					((shownContent.fight&&<img src={fightGif} alt="fighting" loading="lazy" />)||
					(shownContent.construction&&<img src={constructGif} alt="Constructing" loading="lazy" />)||
					(shownContent.smithing&&<img src={smithGif} alt="Smithing" loading="lazy" />))
			}
			{((navigator.appVersion.indexOf("Linux")==-1)&&(navigator.appVersion.indexOf("OPR")==-1))&&<video autoPlay={true} src={shownContent.construction&&constructVideo||shownContent.fight&&fightVideo||shownContent.smithing&&smithVdieo} loop muted />}
			{((navigator.appVersion.indexOf("Linux")!=-1)&&(navigator.appVersion.indexOf("OPR")==-1))&&<video autoPlay={true} src={shownContent.construction&&constructVideo||shownContent.fight&&fightVideo||shownContent.smithing&&smithVdieo} loop muted />}
		</div>
		<div className="ContentRight">
			<div className="CenterContent">
				{shownContent.fight&&<article>
										<h4>{(navigator.language.includes('fr')&&"Bats toi")||(navigator.language.includes('de')&&"Kämpfe")||"Fight"}</h4>
										<p>{(navigator.language.includes('fr')&&"Défendez vos biens en vous battant contre ceux dont l'agitation grandis dans l'ombre de vos réalisations.")||
											(navigator.language.includes('de')&&"Verteidige dein Gut, indem du diejenigen bekämpfst, die im Schatten deiner Errungenschaften unruhig wurden.")||
											"Defend your belongings by fighting those who grew restless in the shadows of your achievements."}</p>
									</article>}
				{shownContent.construction&&<article>
												<h4>{(navigator.language.includes('fr')&&"Construis")||(navigator.language.includes('de')&&"Baue")||"Construct"}</h4>
												<p>{(navigator.language.includes('fr')&&"Laissez grandir votre ville et observez la devenir une des ville les plus ventées de son temps.")||
												(navigator.language.includes('de')&&"Lassen Sie Ihre Stadt zu einer der gepriesenen Städte ihrer Zeit werden.")||
												"Let your town grow out to be one of the most praised city of its time."}</p>
											</article>}
				{shownContent.smithing&&<article>
											<h4>{(navigator.language.includes('fr')&&"Forge")||(navigator.language.includes('de')&&"Schmiede")||"Smith"}</h4>
											<p>{(navigator.language.includes('fr')&&"Utilisez vos compétences pour améliorer la vie du village. Vendez votre artisanat et améliorez vous.")||
											(navigator.language.includes('de')&&"Nutze deine Fähigkeiten, um das Leben der Stadt zu verbessern. Verkaufe dein Handwerk und verbessere dich.")||
											"Use your skills to improve the life of the town. Sell your craftsmanship and improve yourself."}</p>
										</article>}
				<table>
					<tfoot>
						<tr>
							<td>
								<button className={shownContent.fight?"active":"inactive"} onClick={()=>{manageShownContent("fight");}} ></button>
								<button className={shownContent.construction?"active":"inactive"} onClick={()=>{manageShownContent("construction");}} ></button>
								<button className={shownContent.smithing?"active":"inactive"} onClick={()=>{manageShownContent("smithing");}} ></button>
							</td>
						</tr>
					</tfoot>
				</table>
			</div>
		</div>
    </div>
  );
}
				// {shownContent.construction&&<source src={constructVideo} type="video/mp4" />}
				// {shownContent.fight&&<source src={fightVideo} type="video/mp4" />}
				// {shownContent.smithing&&<source src={smithVdieo} type="video/mp4" />}

export default Content;
