import logostudio from './logostudioblanc.png';
import logochef from './cheflogo1.jpg';
import './Menu.css';
import './Menu-mobile.css';

function Menu() {
  return (
    <div className="Menu">
	  <div className="Logo" >
	    <img src={logochef} loading="lazy" alt="Site logo from clker-free-vector-images-3736 on pixabay" />
		<h1>Studios Croustillants </h1>
	  </div>
	  <div className="Items">
        <a href="#Features" >{(navigator.language.includes('fr')&&"Projet")||(navigator.language.includes('de')&&"Projekt")||"Project"}</a>
        <a href="#Technology">{((navigator.language.includes('fr')||navigator.language.includes('de'))&&"Technologie")||"Technology"}</a>
	  </div>
    </div>
  );
}

export default Menu;