import {useState} from 'react';
import './Technology.css';
import './Technology-mobile.css';
import logo from './logo.svg';
import team from './team.jpg';
import teammember from './team_new.jpg';

function Technology() {
	const [shownContent,setShownContent] = useState({tech:false,team:true});
	
	const manageShownContent=(contentToShow)=>{
		for(let cur in shownContent)
			shownContent[cur]=false;
		shownContent[contentToShow]=true;
		setShownContent({...shownContent});
	}
  return (
    <div className="Technology" id="Technology">
		<div className="Team">
			<div className="CenterContent">
				{shownContent.team&&<article> 
										<h4>{(navigator.language.includes('fr')&&"Equipe")||(navigator.language.includes('de')&&"Mannschaft")||"Team"}</h4>
										<p>{(navigator.language.includes('fr')&&"Notre équipe est constitué d'un expert Etienne Rinckel. Notre panoplie d'activité varie de l'ingénieurie 3d à la formation pour adulte et l'enseignement de l'informatique.")||
										(navigator.language.includes('de')&&"Unser Team besteht aus einem Experten Etienne Rinckel. Unser Tätigkeitsspektrum reicht von 3D Ingenieurwesen bis zur Ausbildung und Lehre von Informatik.")||
										"Our team is constituted of one expert Etienne Rinckel. Our range of activities varies from 3d engineering to training and teaching of computer science"}</p>
									</article>}
				{shownContent.tech&&<article>
											<h4>{((navigator.language.includes('fr')||navigator.language.includes('de'))&&"Technologie")||"Technology"}</h4>
											<p>{(navigator.language.includes('fr')&&"Les technologies utiliséent pour les différents projets sont javascript et le framework react." + 
											"Le framework react propose différents outils tel que react-native pour mobile, react-three-fiber pour la 3d et react-node-gui pour des applications de bureau." + 
											"Ceci nous permet de travailler rapidement et produire des programmes performants.")||
											(navigator.language.includes('de')&&"Die verwendeten Technologien in den verschiedenen Projekten sind Javascript und das React-Framework." + 
											"Das React-Framework schlug mehrere Tools wie React-Native für Mobilgeräte, React-Three-Fiber für 3D und React-Node-GUI für Desktop-Apps." + 
											"Dadurch können wir schnell arbeiten und effiziente Programme erstellen.")||
											"The technologies used in the various project are javascript and the react framework." +
												"The react framewok proposes several tools like react-native for mobile, react-three-fiber for 3d and react-node-gui for powerful gui applications." +
											"This allows us to work quickly and produce performant programs."}</p>
										</article>}
				<table>
					<tfoot>
						<tr>
							<td>
								<button className={shownContent.team?"active":"inactive"} onClick={()=>{manageShownContent("team");}} ></button>
								<button className={shownContent.tech?"active":"inactive"} onClick={()=>{manageShownContent("tech");}} ></button>
							</td>
						</tr>
					</tfoot>
				</table>
			</div>
		</div>
		<div className="Tech">
			{shownContent.team&&<img src={teammember} alt="Member of the team" loading="lazy" />}
			{shownContent.tech&&<img src={logo} alt="React logo" loading="lazy" />}
		</div>
    </div>
  );
}

export default Technology;
