import { IoLogoTiktok, IoLogoTwitter, IoLogoDiscord, IoLogoYoutube, IoLogoSteam } from 'react-icons/io5';
import { Link } from 'react-router-dom';
import './Footer.css';
import './Footer-mobile.css';


function Footer(props) {
  return (
    <footer className="Footer">
		<div className="FootLinks" >
			<span onClick={()=>props.showContactForm(true)}>{(navigator.language.includes('fr')&&"Contactez nous")||(navigator.language.includes('de')&&"kontaktiere uns")||"Contact us"}</span>
			<Link to="/webpage/legalmentions">{(navigator.language.includes('fr')&&"Mentions légales")||(navigator.language.includes('de')&&"Rechtliche Erwähnungen ")||"Legal mentions"}</Link>
		</div>
		<div className="FootIcons">
			<a href="https://www.tiktok.com/@studioscroustillants?lang=fr"><IoLogoTiktok /></a>
			<a href="https://twitter.com/StudiosCrousti1"><IoLogoTwitter /></a>
			<a href="https://discord.gg/ZVUMqChT"><IoLogoDiscord /></a>
			<a href="https://www.youtube.com/channel/UCITGGZ226TWJMQWxBjhou5w"><IoLogoYoutube /></a>
			<a href="https://store.steampowered.com"><IoLogoSteam /></a>
		</div>
    </footer>
  );
}
			// <a href="https://cdn.pixabay.com/photo/2014/04/13/20/49/cat-323262_960_720.jpg" >Legal mentions</a>

export default Footer;
