import ReactPlayer from "react-player"
import './Video.css';

function Video() {
  return (
    <div className="Video" >
	  <ReactPlayer width={window.innerWidth+"px"} height={(window.innerWidth<640?Math.ceil(window.innerHeight/3):(window.innerHeight-50))+"px"} muted={true} playing={true}
        url="https://youtu.be/CqXss5V0d9s"
      />
    </div>
  );
}

export default Video;
