import { BrowserRouter, Route, Switch, Link } from "react-router-dom";
import App from './App';
import LegalMentions from './LegalMentions';

const Routeur = () => {
	return(
	<BrowserRouter>
        <Switch>
          <Route path="/webpage/legalmentions">
            <LegalMentions />
          </Route>
          <Route path="/">
            <App />
          </Route>
        </Switch>
    </BrowserRouter>
	)
	
}

export default Routeur